
import {authorizeLogin, wxLogin, guidelist,dynamicList,categoryList,indexShopList} from '../../api'
import Panel from 'vux/src/components/panel/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import Storage from 'good-storage'
import { fail } from 'assert';
export default {
name: 'index',
data() {
return {
onFetching: false,
animate:false,
title:'组合标题',
type: '1',
guideList:[],
dynamicList:[],
categoryList:[],
shopList:[],
tabWidth: 0,
isActive:0,
cid:1,
pagesize: 10,
page: 1,
IslastPage: false,
isSearchFixed:false,
isTabFixed:false,
tabboxWidth: 0,
scrollTop: 0,
scrollHeightBox: '0px',
scrollBoxHeight: 0,
scrollHeight: '200px',
clientHeight: 0,
top: 0,
scrollH: 0
}
},
components: {
Panel,
Tab,
TabItem,
Scroller,
LoadMore
},
created() {
setInterval(this.scroll,3000)
},
methods: {
toDetail(item){
this.$store.commit('SET_INFOMATION',item)

this.$router.push({name: 'Detail', params:{id : item.item_id,info:item.item_coupon.info}});
},

wipeBlank(value) {
return value.trim()
},

onScrollBottom () {
let that = this
if (this.onFetching || this.IslastPage || that.page >=that.lastPage) {
return false
} else {
that.onFetching = true
var page =  ++ that.page
let data = {
cid:that.cid,
pagesize:that.pagesize,
page: page
}
indexShopList(data).then(res =>{
if(res.code === 0){
let IslastPage = false
if(that.page >= res.result.last_page)
IslastPage = true
that.shopList = that.shopList.concat(res.result.data)
that.page = res.result.current_page
that.lastPage = res.result.last_page
that.onFetching = true
that.IslastPage = IslastPage
}
that.onFetching = false
that.$nextTick(() => {
that.$refs.scrollerBottom.reset()
})
})
}
},
onScroll (pos) {
let offTop = pos.top

window.scrollTo(0,328) 
},

handleScroll () {
var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
if(scrollTop >= 100){

this.isSearchFixed = true
this.isTabFixed = false
if(scrollTop > this.scrollH){

this.isSearchFixed = true
this.isTabFixed = true
}
}else{

this.isSearchFixed = false
this.isTabFixed = false
}
},
scroll(){
this.animate=true;    
setTimeout(()=>{      //  这里直接使用了es6的箭头函数，省去了处理this指向偏移问题，代码也比之前简化了很多
this.dynamicList.push(this.dynamicList[0]);  
this.dynamicList.shift();               
this.animate=false;  
},500)
},
getGuideList(){
let that = this
guidelist().then(res =>{
if(res.code === 0){
that.guideList = res.result
}
})
},
getdynamicList(){
let that = this
dynamicList().then(res =>{
if(res.code === 0){
that.dynamicList = res.result
}
})
},
getCategray(){
let that = this
categoryList().then(res =>{
if(res.code === 0){
that.categoryList = res.result.data
that.tabWidth = res.result.data.length*5 + 'rem'
}
})
},
getShopList(data){
let that = this
if (that.onFetching) return;
that.onFetching = true
indexShopList(data).then(res =>{
if(res.code === 0){
that.shopList = res.result.data
that.page = res.result.current_page
that.lastPage = res.result.last_page
that.onFetching = false
that.IslastPage = false
}
that.onFetching = false
})
},
checkTabs(item,index){
let that = this
that.$nextTick(() => {
that.$refs.scrollerBottom.reset({top: 0})
})
that.isActive = index
that.cid = item
that.page = 1
that.isSearchFixed = true
that.isTabFixed = true
window.scrollTo(0,328) 
let data = {
cid:that.cid,
pagesize:that.pagesize,
page:that.page
}
that.getShopList(data)
},
toSearch(e){
this.$router.push({path:'search'})
}
},
mounted () {
let that = this
window.addEventListener('scroll', this.handleScroll)
this.$nextTick(() => {
that.clientHeight = `${document.documentElement.clientHeight}`
that.scrollH = this.$refs.header.offsetHeight + this.$refs.adbox.offsetHeight + this.$refs.tab_box.offsetHeight - this.$refs.search.offsetHeight
that.scrollHeight = (that.clientHeight - that.$refs.search.offsetHeight - that.$refs.tab_box.offsetHeight) + 'px'
this.$refs.scrollerBottom.reset({top: 0})
window.scrollTo(0,0)
})
let data = {
cid:this.cid,
pagesize:this.pagesize,
page:1
}
this.getGuideList()
this.getdynamicList()
this.getCategray()
this.getShopList(data)
}
}




